import {source} from './universalSettings.js'



export async function savesnapshotandgetsharablelink(formData){
    return fetch('source'+'/documentation/savesnapshotandgetsharablelink',{method: 'POST',body: formData}).then(res=>{
                    return res.json()
                })
}

